<template>
  <div class="splash-page">
      <div class="d-block text-right top-content">
          <span class="cy-logo"></span>
          <span class="cy-name">{{$t("Splash.CY_NAME")}}</span>
          <span class="cy-description">{{$t("Splash.CY_DESCRIPTION")}}</span>
          <span class="cy-splash-loading"></span>
      </div>
      <div class="d-block bottom-content">
          <span class="cy-splash-bussiness"></span>
      </div>
  </div>
</template>

<script>
import {LOGIN} from '@/mutations/Auth.mutation';

export default {
  name: 'SplashPage',
  mounted: async function () {
    // this.$store.dispatch(LOGIN, {...this.FormData});
    try {
       if (this.$StoreService.get('user')) {
      setTimeout(() => this.$router.push('/home'), process.env.VUE_APP_SPLASH_TIMED_OUT);
    } else {
      setTimeout(() => this.$router.push('/auth/login'), process.env.VUE_APP_SPLASH_TIMED_OUT);
    }
    } catch (error) {
      console.log(11, error)
    }
   
  }
}
</script>
